.footer {
  background: rgba(0, 0, 0, 0.95);
  backdrop-filter: blur(10px);
  border-top: 1px solid rgba(0, 255, 0, 0.2);
  color: #ccc;
  font-family: 'Courier New', monospace;
  padding: 40px 20px 20px 20px;
  margin-top: 60px;
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

/* Time Section */
.time-section {
  margin-bottom: 30px;
}

.current-time {
  font-size: 2.5rem;
  font-weight: bold;
  color: #00ff00;
  text-shadow: 0 0 10px rgba(0, 255, 0, 0.5);
  margin-bottom: 8px;
  letter-spacing: 0.1em;
}

.current-date {
  font-size: 1.1rem;
  color: #888;
  font-weight: normal;
  letter-spacing: 0.05em;
}

/* Timeline */
.timeline {
  width: 100%;
  display: flex;
  justify-content: center;
  margin: 20px 0;
}

.timeline-line {
  width: 80%;
  max-width: 600px;
  height: 2px;
  background: linear-gradient(90deg, transparent 0%, #00ff00 20%, #00ff00 80%, transparent 100%);
  box-shadow: 0 0 10px rgba(0, 255, 0, 0.3);
  position: relative;
}

.timeline-line::before {
  content: '';
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 8px;
  height: 8px;
  background: #00ff00;
  border-radius: 50%;
  box-shadow: 0 0 15px rgba(0, 255, 0, 0.6);
}

/* Copyright Section */
.copyright-section {
  margin-top: 0%;
}

.copyright {
  font-size: 0.9rem;
  color: #888;
  margin-bottom: 8px;
  letter-spacing: 0.05em;
}

.made-by {
  font-size: 1rem;
  color: #ccc;
  letter-spacing: 0.05em;
}

.highlight {
  color: #00ff00;
  font-weight: bold;
  text-shadow: 0 0 8px rgba(0, 255, 0, 0.4);
  text-transform: uppercase;
  letter-spacing: 0.1em;
}

/* Hacker Theme Support */
.homepageContainer.hacker-theme .footer {
  border-top: 1px solid rgba(50, 85, 110, 0.2);
}

.homepageContainer.hacker-theme .current-time {
  color: #32556e;
  text-shadow: 0 0 10px rgba(50, 85, 110, 0.5);
}

.homepageContainer.hacker-theme .timeline-line {
  background: linear-gradient(90deg, transparent 0%, #32556e 20%, #32556e 80%, transparent 100%);
  box-shadow: 0 0 10px rgba(50, 85, 110, 0.3);
}

.homepageContainer.hacker-theme .timeline-line::before {
  background: #32556e;
  box-shadow: 0 0 15px rgba(50, 85, 110, 0.6);
}

.homepageContainer.hacker-theme .highlight {
  color: #32556e;
  text-shadow: 0 0 8px rgba(50, 85, 110, 0.4);
}

/* Responsive Design */
@media (max-width: 768px) {
  .footer {
    padding: 30px 15px 15px 15px;
    margin-top: 40px;
  }
  
  .current-time {
    font-size: 2rem;
  }
  
  .current-date {
    font-size: 1rem;
  }
  
  .timeline-line {
    width: 90%;
  }
}

@media (max-width: 480px) {
  .footer {
    padding: 25px 10px 10px 10px;
    margin-top: 30px;
  }
  
  .current-time {
    font-size: 1.7rem;
  }
  
  .current-date {
    font-size: 0.9rem;
  }
  
  .timeline-line {
    width: 95%;
  }
  
  .copyright,
  .made-by {
    font-size: 0.85rem;
  }
}

@media (max-width: 360px) {
  .current-time {
    font-size: 1.5rem;
  }
  
  .current-date {
    font-size: 0.8rem;
  }
  
  .copyright,
  .made-by {
    font-size: 0.8rem;
  }
}

@media (max-width: 320px) {
  .current-time {
    font-size: 1.3rem;
  }
  
  .current-date {
    font-size: 0.75rem;
  }
  
  .copyright,
  .made-by {
    font-size: 0.75rem;
  }
}