.tabbed-info-card {
  background: transparent;
  margin: 40px 0;
  font-family: 'Courier New', monospace;
}

.tab-header {
  display: flex;
  gap: 40px;
  align-items: center;
  margin-bottom: 30px;
  position: relative;
}

.tab-header::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 0;
  right: 0;
  height: 1px;
  background: #555;
}

.tab-btn {
  background: transparent;
  color: #888;
  border: none;
  padding: 10px 0;
  font-size: 1.3rem;
  font-family: 'Courier New', monospace;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  position: relative;
  font-weight: bold;
}

.tab-btn::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 0;
  right: 0;
  height: 3px;
  background: transparent;
  transition: all 0.3s ease;
}

.tab-btn:hover {
  color: #00ff00;
}

.tab-btn.active {
  color: #00ff00;
  text-shadow: 0 0 10px rgba(0, 255, 0, 0.5);
}

.tab-btn.active::after {
  background: #00ff00;
  box-shadow: 0 0 10px rgba(0, 255, 0, 0.5);
}

.tab-body {
  background: linear-gradient(135deg, rgba(26, 26, 26, 0.8) 0%, rgba(42, 42, 42, 0.8) 100%);
  border: 1px solid rgba(85, 85, 85, 0.3);
  border-radius: 15px;
  padding: 30px;
  /* Remove fixed min-height and let content determine height */
  backdrop-filter: blur(10px);
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.tab-content {
  color: #ccc;
  font-family: 'Courier New', monospace;
  /* Ensure content takes only needed space */
  height: fit-content;
}

/* Education Content */
.education-content {
  text-align: left;
  height: fit-content;
}

.education-details {
  flex: 1 1 auto;   /* allow it to grow when there's space */
  min-width: 0;     /* allow children to shrink properly */
  max-width: 100%;  /* let the block use available width instead of forcing 300px */
  width: auto;
}

.education-details p {
  margin: 24px 0;                /* increase vertical gap between School / University / Degree lines */
  font-size: 1.05rem;
  line-height: 1.4;
  text-align: left;

  /* Keep label and value on same row when there's room */
  display: flex;
  gap: 12px;
  align-items: flex-start;
  flex-wrap: wrap;              /* allow value to wrap when space is insufficient */
  white-space: normal;
  min-width: 0;                 /* allow flex children to shrink */
}

.education-details strong {
  color: #00ff00;
  text-shadow: 0 0 5px rgba(0, 255, 0, 0.3);
  display: inline-flex;
  flex: 0 0 auto;               /* label stays compact */
  min-width: 0;                 /* don't force big minimum width */
  margin-right: 4px;
}

/* Make the value portion flexible so it sits on same line when possible */
.education-details p > *:not(strong) {
  flex: 1 1 auto;
  min-width: 0;
  white-space: normal;
}

/* Education Buttons Section */
.education-buttons {
  display: flex;
  flex-direction: row;
  gap: 15px;
  margin-top: 18px;      /* controls distance from last education row to buttons */
  margin-bottom: 6px;    /* reduces space under the buttons */
  justify-content: flex-start;
  flex-wrap: nowrap;
}

.education-btn {
  background: transparent;
  color: #00ff00;
  border: 2px solid rgba(0, 255, 0, 0.3);
  border-radius: 8px;
  padding: 10px 16px;
  font-size: 0.9rem;
  font-family: 'Courier New', monospace;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-weight: bold;
  text-shadow: 0 0 5px rgba(0, 255, 0, 0.3);
  display: flex;
  align-items: center;
  gap: 8px;
  min-width: 140px;
  justify-content: center;
  flex-shrink: 1;
}

.education-btn:hover {
  border-color: #00ff00;
  background: rgba(0, 255, 0, 0.1);
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 255, 0, 0.2);
}

.education-btn:active {
  transform: translateY(0);
  box-shadow: 0 3px 10px rgba(0, 255, 0, 0.3);
}

/* Documents container - align both to right horizontally */
.documents-container {
  display: flex;
  gap: 30px;
  align-items: flex-start;
  flex-shrink: 0;
}

.document-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  flex-shrink: 0;
}

.document-section p {
  color: #00ff00;
  margin-bottom: 10px;
  font-weight: bold;
  font-size: 1rem;
}

.docThumbnail {
  width: 140px;
  border: 2px solid rgba(0, 255, 0, 0.5);
  border-radius: 10px;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.docThumbnail:hover {
  transform: scale(1.05) translateY(-5px);
  border-color: #00ff00;
  box-shadow: 0 8px 25px rgba(0, 255, 0, 0.2);
}

/* Certificate wrapper for new layout */
.certificateWrapper {
  display: flex;
  flex-direction: column;
  min-width: 280px;
  width: 280px;
  flex-shrink: 0;
  flex-grow: 0;
}

.certificateBox {
  position: relative;
  width: 100%;
  height: 200px;
  border-radius: 12px;
  overflow: hidden;
  text-decoration: none;
  color: inherit;
  transition: all 0.3s ease;
  border: 2px solid rgba(0, 255, 0, 0.3);
  display: block;
}

.certificateBox:hover {
  border-color: #00ff00;
  box-shadow: 0 8px 25px rgba(0, 255, 0, 0.3);
}

.certificateImage {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  filter: none; /* Remove blur */
}

.certificateContent {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  background: transparent; /* Remove background overlay */
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  backdrop-filter: none; /* Remove backdrop filter */
}

/* Certificate info below the image */
.certificateInfo {
  padding: 10px 5px;
  background: transparent;
}

.certificateTitle {
  color: #00ff00;
  font-size: 0.9rem;
  font-weight: bold;
  margin-bottom: 5px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
  line-height: 1.2;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  word-wrap: break-word;
  hyphens: auto;
  white-space: normal;
  height: auto;
  max-height: 2.4em;
}

.certificateIssuer {
  color: #ffffff;
  font-size: 0.75rem;
  font-weight: bold;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, 0.8);
  margin: 0;
}

/* Category badge */
.certificateContent::before {
  content: attr(data-category);
  position: absolute;
  top: 10px;
  right: 10px;
  background: rgba(0, 255, 0, 0.8);
  color: #000;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 0.7rem;
  font-weight: bold;
  text-transform: uppercase;
  z-index: 10;
}

/* Responsive Design for Certificate Layout */
@media (max-width: 768px) {
  .certificateWrapper {
    min-width: 240px;
    width: 240px;
  }
  
  .certificateBox {
    height: 180px;
  }
  
  .certificateTitle {
    font-size: 0.85rem;
    line-height: 1.1;
    max-height: 2.2em;
  }
  
  .certificateIssuer {
    font-size: 0.7rem;
  }
}

@media (max-width: 480px) {
  .certificateWrapper {
    min-width: 220px;
    width: 220px;
  }
  
  .certificateBox {
    height: 170px;
  }
  
  .certificateTitle {
    font-size: 0.8rem;
    line-height: 1.1;
    margin-bottom: 3px;
    max-height: 2.2em;
  }
  
  .certificateIssuer {
    font-size: 0.65rem;
  }
}

@media (max-width: 360px) {
  .certificateWrapper {
    min-width: 200px;
    width: 200px;
  }
  
  .certificateBox {
    height: 160px;
  }
  
  .certificateTitle {
    font-size: 0.75rem;
    line-height: 1.0;
    margin-bottom: 2px;
    max-height: 2.0em;
  }
  
  .certificateIssuer {
    font-size: 0.6rem;
  }
}

@media (max-width: 320px) {
  .certificateWrapper {
    min-width: 180px;
    width: 180px;
  }
  
  .certificateBox {
    height: 150px;
  }
  
  .certificateTitle {
    font-size: 0.7rem;
    line-height: 1.0;
    max-height: 2.0em;
  }
  
  .certificateIssuer {
    font-size: 0.55rem;
  }
}

/* Skills Content - Replace skill bars with skill logos */
.skills-content {
  padding: 20px 0;
  height: fit-content;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
  gap: 25px;
  justify-items: center;
  padding: 10px 0;
}

.skill-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding: 15px;
  border: 2px solid rgba(0, 255, 0, 0.3);
  border-radius: 12px;
  background: rgba(42, 42, 42, 0.3);
  transition: all 0.3s ease;
  min-width: 100px;
  backdrop-filter: blur(5px);
}

.skill-item:hover {
  transform: translateY(-5px);
  border-color: #00ff00;
  box-shadow: 0 8px 25px rgba(0, 255, 0, 0.2);
  background: rgba(42, 42, 42, 0.5);
}

.skill-logo-container {
  width: 50px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 10px;
}

.skill-logo {
  width: 100%;
  height: 100%;
  object-fit: contain;
  filter: drop-shadow(0 0 5px rgba(0, 255, 0, 0.3));
  transition: all 0.3s ease;
}

.skill-item:hover .skill-logo {
  filter: drop-shadow(0 0 10px rgba(0, 255, 0, 0.5));
  transform: scale(1.1);
}

.skill-name {
  color: #00ff00;
  font-weight: bold;
  font-size: 0.9rem;
  font-family: 'Courier New', monospace;
  text-shadow: 0 0 5px rgba(0, 255, 0, 0.3);
  letter-spacing: 0.05em;
}

/* Remove old skill bar styles */
.skillBar,
.barTrack,
.barFill,
.barPercent {
  display: none;
}

/* Certificates Content */
.certificates-content {
  padding: 10px 0;
  height: fit-content; /* Remove extra space */
}

/* Category buttons with smaller text and green color highlighting */
.categoryButtons {
  display: flex;
  gap: 30px;
  align-items: center;
  margin-bottom: 30px;
  position: relative;
}

.categoryButtons::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 0;
  right: 0;
  height: 1px;
  background: #555;
}

.categoryBtn {
  background: transparent;
  color: #888;
  border: none;
  padding: 8px 0;
  font-size: 0.9rem;
  font-family: 'Courier New', monospace;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  position: relative;
  font-weight: bold;
}

.categoryBtn::after {
  content: '';
  position: absolute;
  bottom: -8px;
  left: 0;
  right: 0;
  height: 3px;
  background: transparent;
  transition: all 0.3s ease;
}


.categoryBtn.active {
  color: #00ff00;
}

.categoryBtn.active::after {
  background: #00ff00;
  box-shadow: 0 0 10px rgba(0, 255, 0, 0.5);
}

/* Horizontal scrolling certificates */
.certificatesList {
  display: flex;
  gap: 20px;
  overflow-x: auto;
  overflow-y: hidden;
  padding: 20px 0 10px 10px;
  scroll-behavior: smooth;
  white-space: nowrap;
  height: fit-content;
}

/* Thin scrollbar styling */
.certificatesList::-webkit-scrollbar {
  height: 6px; /* Make scrollbar thinner (was probably 12px or more) */
  width: 6px;
}

.certificatesList::-webkit-scrollbar-track {
  background: rgba(26, 26, 26, 0.5);
  border-radius: 3px;
}

.certificatesList::-webkit-scrollbar-thumb {
  background: rgba(0, 255, 0, 0.7);
  border-radius: 3px;
  transition: all 0.3s ease;
}

.certificatesList::-webkit-scrollbar-thumb:hover {
  background: #00ff00;
  box-shadow: 0 0 10px rgba(0, 255, 0, 0.2);
}

/* For Firefox */
.certificatesList {
  scrollbar-width: thin;
  scrollbar-color: rgba(0, 255, 0, 0.7) rgba(26, 26, 26, 0.5);
}

/* Hacker theme scrollbar */
.tabbed-info-card.hacker-theme .certificatesList::-webkit-scrollbar-thumb {
  background: rgba(50, 85, 110, 0.7);
}

.tabbed-info-card.hacker-theme .certificatesList::-webkit-scrollbar-thumb:hover {
  background: #32556e;
  box-shadow: 0 0 10px rgba(50, 85, 110, 0.2);
}

.tabbed-info-card.hacker-theme .certificatesList {
  scrollbar-color: rgba(50, 85, 110, 0.7) rgba(26, 26, 26, 0.5);
}

/* Responsive Design */
@media (max-width: 768px) {
  .tab-header {
    flex-direction: column;
    gap: 20px;
    align-items: flex-start;
  }
  
  .tab-btn {
    font-size: 1.1rem;
  }
  
  .categoryButtons {
    gap: 20px;
    flex-wrap: wrap;
  }
  
  .categoryBtn {
    font-size: 0.8rem;
  }
  
  .tab-body {
    padding: 20px;
  }
  
  .certificateBox {
    min-width: 240px;
    width: 240px;
    height: 180px;
  }
  
  .certificateTitle {
    font-size: 1rem;
  }
  
  .certificateIssuer {
    font-size: 0.75rem;
  }
  
  .skillBar {
    gap: 15px;
  }
  
  .skillBar span:first-child {
    width: 100px;
    font-size: 0.9rem;
  }
  
  .education-buttons {
    gap: 12px;
    margin-top: 18px;
  }
  
  .education-btn {
    padding: 9px 15px;
    font-size: 0.85rem;
    min-width: 120px;
  }
}

@media (max-width: 480px) {
  .education-buttons {
    gap: 10px;
    margin-top: 15px;
  }
  
  .education-btn {
    padding: 8px 12px;
    font-size: 0.8rem;
    min-width: 110px;
  }
}

@media (max-width: 360px) {
  .education-buttons {
    gap: 8px;
    margin-top: 12px;
  }
  
  .education-btn {
    padding: 6px 10px;
    font-size: 0.75rem;
    min-width: 100px;
    letter-spacing: 0.02em;
  }
}

@media (max-width: 300px) {
  .education-buttons {
    flex-direction: row;
    gap: 8px;
    align-items: center;
  }
  
  .education-btn {
    min-width: 90px;
    padding: 6px 8px;
    font-size: 0.7rem;
  }
}

