/* Navigation Bar */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.95);
  backdrop-filter: blur(10px);
  border-bottom: 1px solid rgba(0, 255, 0, 0.2);
  z-index: 1000;
  padding: 15px 0;
}

.nav-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

.nav-logo {
  display: flex;
  align-items: center;
  padding-left: 0%;
  flex: 0 0 auto; /* Add this to prevent flex shrinking */
  min-width: 135px; /* Ensure minimum space for logo */
}

.logo {
  width: 115px;
  height: 50px;
  object-fit: contain;
  transition: all 0.3s ease;
}

.logo:hover {
  transform: scale(1.1);
}

.nav-links {
  display: flex;
  justify-content: center;
  gap: 50px;
  flex: 1;
  margin: 0 20px; /* Add margin to create space from logo */
}

.nav-btn {
  background: transparent;
  color: #888;
  border: none;
  padding: 10px 20px;
  font-size: 1rem;
  font-family: 'Courier New', monospace;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-weight: bold;
  position: relative;
}

.nav-btn::after {
  content: '';
  position: absolute;
  bottom: 5px;
  left: 50%;
  transform: translateX(-50%);
  width: 0;
  height: 2px;
  background: #00ff00;
  transition: width 0.3s ease;
}

.nav-btn:hover {
  color: #00ff00;
  text-shadow: 0 0 5px rgba(0, 255, 0, 0.3);
}

.nav-btn:hover::after {
  width: 80%;
}

