}

/* Image Container - Center the container */
.headerImageContainer {
  flex: 0.4 0 auto;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  margin-top: 0;
}

/* Image Flip Animation Styles - Center the flip container */
.image-flip-container {
  width: 350px;
  height: 350px;
  perspective: 1000px;
  cursor: pointer;
}

.image-flip-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.6s;
  transform-style: preserve-3d;
}

/* Remove hover trigger and add flipped class */
.image-flip-container.flipped .image-flip-inner {
  transform: rotateY(180deg);
}

.image-flip-front,
.image-flip-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border-radius: 50%;
  overflow: hidden;
}

.image-flip-front {
  z-index: 2;
}

.image-flip-back {
  transform: rotateY(180deg);
  z-index: 1;
}

.homepageImage {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: none;
  object-fit: cover;
  box-shadow: none;
  transition: none;
}

