  align-self: flex-start;
}

.form-title {
  color: #00ff00;
  font-size: 1.4rem;
  font-family: 'Courier New', monospace;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-weight: bold;
  margin-bottom: 20px;
  text-shadow: 0 0 10px rgba(0, 255, 0, 0.5);
  text-align: center;
}

.contact-form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.form-group {
  display: flex;
  flex-direction: column;
}

.form-group input,
.form-group textarea {
  background: rgba(0, 0, 0, 0.4);
  border: 1px solid rgba(0, 255, 0, 0.3);
  border-radius: 8px;
  padding: 10px 14px;
  color: #ccc;
  font-family: 'Courier New', monospace;
  font-size: 0.9rem;
  transition: all 0.3s ease;
  resize: vertical;
}

.form-group input:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #00ff00;
  box-shadow: 0 0 10px rgba(0, 255, 0, 0.2);
  background: rgba(0, 0, 0, 0.6);
}

.form-group input::placeholder,
.form-group textarea::placeholder {
  color: #888;
  font-size: 0.9rem;
}

/* Captcha Container */
.captcha-container {
  display: flex;
  justify-content: center;
  margin: 15px 0;
}

.captcha-container > div {
  transform-origin: center;
}

.verification {
  flex-direction: row;
  align-items: center;
  gap: 10px;
  margin-top: 8px;
}

.checkbox-container {
  display: flex;
  align-items: center;
  cursor: pointer;
  font-family: 'Courier New', monospace;
  color: #ccc;
  font-size: 0.9rem;
}

.checkbox-container input {
  display: none;
}

.checkmark {
  width: 18px;
  height: 18px;
  border: 2px solid rgba(0, 255, 0, 0.3);
  border-radius: 4px;
  margin-right: 10px;
  position: relative;
  transition: all 0.3s ease;
}

.checkbox-container input:checked + .checkmark {
  background: #00ff00;
  border-color: #00ff00;
}

.checkbox-container input:checked + .checkmark::after {
  content: '';
  position: absolute;
  color: #000;
  font-weight: bold;
  font-size: 12px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.checkbox-container:hover .checkmark {
  border-color: #00ff00;
  box-shadow: 0 0 5px rgba(0, 255, 0, 0.2);
}

.send-btn {
  background: transparent;
  color: #00ff00;
  border: 2px solid rgba(0, 255, 0, 0.3);
  border-radius: 8px;
  padding: 10px 25px;
  font-size: 0.95rem;
  font-family: 'Courier New', monospace;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-weight: bold;
  text-shadow: 0 0 5px rgba(0, 255, 0, 0.3);
  margin-top: 8px;
}

.send-btn:hover {
  border-color: #00ff00;
  background: rgba(0, 255, 0, 0.1);
  box-shadow: 0 6px 20px rgba(0, 255, 0, 0.2);
}

.send-btn:active {
  transform: translateY(0);
  box-shadow: 0 3px 10px rgba(0, 255, 0, 0.3);
}

.send-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* Submit Status Messages */
.submit-message {
  padding: 12px 15px;
  margin-bottom: 15px;
  border-radius: 6px;
  font-family: 'Courier New', monospace;
  font-size: 0.95rem;
  font-weight: 500;
  text-align: center;
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.success-message {
  background: rgba(0, 255, 0, 0.1);
  border: 1px solid rgba(0, 255, 0, 0.3);
  color: #00ff00;
}

.error-message {
  background: rgba(255, 0, 0, 0.1);
  border: 1px solid rgba(255, 0, 0, 0.3);
  color: #ff4444;
}



/* Contact Message Styles */
.contact-message {
  border: 2px solid rgba(0, 255, 0, 0.3);
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 8%;
  background: rgba(42, 42, 42, 0.2);
  backdrop-filter: blur(5px);
  transition: all 0.3s ease;
}

.contact-message:hover {
  border-color: #00ff00;
  box-shadow: 0 8px 25px rgba(0, 255, 0, 0.2);
