/* AdminLogin.css */
.admin-login-container {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 20px;
}

.admin-login-box {
  background: white;
  border-radius: 16px;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  padding: 40px;
  width: 100%;
  max-width: 420px;
  animation: slideIn 0.5s ease-out;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.admin-login-title {
  font-size: 2rem;
  color: #333;
  margin-bottom: 8px;
  text-align: center;
}

.admin-login-subtitle {
  color: #666;
  text-align: center;
  margin-bottom: 30px;
  font-size: 0.95rem;
}

.admin-login-form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.admin-login-form .form-group {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.admin-login-form label {
  font-weight: 600;
  color: #333;
  font-size: 0.9rem;
}

.admin-login-form input {
  padding: 12px 16px;
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s ease;
  outline: none;
}

.admin-login-form input:focus {
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.admin-login-form input:disabled {
  background-color: #f5f5f5;
  cursor: not-allowed;
}

.error-message {
  background-color: #fee;
  color: #c33;
  padding: 12px;
  border-radius: 8px;
  border-left: 4px solid #c33;
  font-size: 0.9rem;
}

.login-button {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  border: none;
  padding: 14px;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 10px;
}

.login-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 10px 20px rgba(102, 126, 234, 0.3);
}

.login-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.back-button {
  background: transparent;
  color: #666;
  border: none;
  padding: 12px;
  margin-top: 20px;
  font-size: 0.9rem;
  cursor: pointer;
  transition: color 0.3s ease;
}

.back-button:hover:not(:disabled) {
  color: #333;
}

.back-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

@media (max-width: 480px) {
  .admin-login-box {
    padding: 30px 20px;
  }

  .admin-login-title {
    font-size: 1.6rem;
  }
}
