/* Container and Background */
.hacker-container {
  min-height: auto; /* Changed from 100vh */
  background: transparent; /* Changed from #000000 */
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem; /* Reduced from 2rem */
  font-family: 'Courier New', monospace;
}

.hacker-frame {
  position: relative;
}

/* Corner Brackets */
.corner-bracket {
  position: absolute;
  width: 1.5rem; /* Reduced from 2rem */
  height: 1.5rem; /* Reduced from 2rem */
  z-index: 10;
  animation: pulse 2s infinite;
}

.corner-top-left {
  top: -0.75rem; /* Adjusted for smaller brackets */
  left: -0.75rem;
  border-left: 2px solid #00ff00;
  border-top: 2px solid #00ff00;
}

.corner-top-right {
  top: -0.75rem;
  right: -0.75rem;
  border-right: 2px solid #00ff00;
  border-top: 2px solid #00ff00;
}

.corner-bottom-left {
  bottom: -0.75rem;
  left: -0.75rem;
  border-left: 2px solid #00ff00;
  border-bottom: 2px solid #00ff00;
}

.corner-bottom-right {
  bottom: -0.75rem;
  right: -0.75rem;
  border-right: 2px solid #00ff00;
  border-bottom: 2px solid #00ff00;
}

/* Main Container */
.main-container {
  position: relative;
  background: #000000;
  border: 1px solid #00ff00;
  padding: 1.5rem; /* Reduced from 2rem */
  min-width: 18rem; /* Reduced from 24rem */
  box-shadow: 0 0 20px rgba(0, 255, 0, 0.2);
}

/* Glowing Border Effects */
.glow-border-outer {
  position: absolute;
  inset: 0;
  border: 1px solid #00ff00;
  animation: pulse 2s infinite;
  opacity: 0.5;
}

.glow-border-inner {
  position: absolute;
  inset: 4px;
  border: 1px solid rgba(0, 255, 0, 0.3);
}

/* Scanning Line Effect */
.scan-container {
  position: absolute;
  inset: 0;
  overflow: hidden;
}

.scanning-line {
  position: absolute;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, transparent, #00ff00, transparent);
  animation: scan 3s linear infinite;
}

/* Content */
.content {
  position: relative;
  z-index: 10;
}

.title {
  color: #00ff00;
  font-size: 1.25rem; /* Reduced from 1.5rem */
  margin-bottom: 1.5rem; /* Reduced from 2rem */
  text-align: center;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  text-shadow: 0 0 10px #00ff00, 0 0 20px #00ff00, 0 0 30px #00ff00;
}

.bracket {
  animation: pulse 2s infinite;
}

.title-text {
  margin: 0 0.5rem;
}

/* Info Fields */
.info-fields {
  display: flex;
  flex-direction: column;
  gap: 1rem; /* Reduced from 1.5rem */
}

.info-card {
  background: transparent;
  margin: 0;
  font-family: 'Courier New', monospace;
  width: 100%;
}

.info-header {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
  position: relative;
}

.info-header::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 0;
  right: 0;
  height: 1px;
  background: #555;
}

.info-title {
  background: transparent;
  color: #00ff00;
  border: none;
  padding: 10px 0;
  font-size: 1.3rem;
  font-family: 'Courier New', monospace;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  position: relative;
  font-weight: bold;
  margin: 0;
  text-shadow: 0 0 10px rgba(0, 255, 0, 0.5);
}

.info-title::after {
  content: '';
  position: absolute;
  bottom: -10px;
  left: 0;
  right: 0;
  height: 3px;
  background: #00ff00;
  box-shadow: 0 0 10px rgba(0, 255, 0, 0.5);
}

.info-body {
  background: linear-gradient(135deg, rgba(26, 26, 26, 0.8) 0%, rgba(42, 42, 42, 0.8) 100%);
  border: 1px solid rgba(85, 85, 85, 0.3);
  border-radius: 15px;
  padding: 30px;
  min-height: 200px;
  backdrop-filter: blur(10px);
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.3),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
}

.info-content {
  color: #ccc;
  font-family: 'Courier New', monospace;
}

.info-item {
  margin-bottom: 15px;
}

.info-item p {
  margin: 0;
  font-size: 1.1rem;
  line-height: 0.1;
  text-align: left;
}

.info-item strong {
  color: #00ff00;
  text-shadow: 0 0 5px rgba(0, 255, 0, 0.3);
  display: inline-block;
  min-width: 80px;
}

.side-indicator {
  position: absolute;
  left: -0.5rem;
  top: 0;
  bottom: 0;
  width: 4px;
  background: #00ff00;
  animation: pulse 2s infinite;
}

/* Main Info Box */
.info-box {
  background: linear-gradient(135deg, rgba(26, 26, 26, 0.9) 0%, rgba(42, 42, 42, 0.9) 100%);
  border: 1px solid rgba(85, 85, 85, 0.4);
  border-radius: 12px;
  padding: 25px 35px;
  backdrop-filter: blur(10px);
  box-shadow: 
    0 8px 32px rgba(0, 0, 0, 0.4),
    inset 0 1px 0 rgba(255, 255, 255, 0.1);
  font-family: 'Courier New', monospace;
  color: #ccc;
  width: 100%;
  max-width: 650px;
  min-width: 500px;
  min-height: 220px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.info-box h3 {
  color: #00ff00;
  font-size: 1.5rem;
  margin-bottom: 25px;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-weight: bold;
  text-shadow: 0 0 10px rgba(0, 255, 0, 0.5);
  text-align: center;
}

.info-content {
  display: flex;
  flex-direction: column;
  gap: 18px;
  flex-grow: 1;
}

.info-item {
  display: flex;
  flex-direction: column;
  gap: 6px;
  padding-bottom: 8px;
}

.info-label {
  color: #00ff00;
  font-size: 1rem;
  font-weight: bold;
  text-shadow: 0 0 5px rgba(0, 255, 0, 0.3);
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.info-value {
  color: #fff;
  font-size: 1.1rem;
  font-weight: normal;
  margin-left: 12px;
}

/* Download Buttons Section */
.download-buttons {
  display: flex;
  flex-direction: row;
  gap: 15px;
  margin-top: 20px;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.download-btn {
  background: transparent;
  color: #00ff00;
  border: 2px solid rgba(0, 255, 0, 0.3);
  border-radius: 8px;
  padding: 10px 16px;
  font-size: 0.9rem;
  font-family: 'Courier New', monospace;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  font-weight: bold;
  text-shadow: 0 0 5px rgba(0, 255, 0, 0.3);
  display: flex;
  align-items: center;
  gap: 8px;
  min-width: 140px;
  justify-content: center;
}

.download-btn:hover {
  border-color: #00ff00;
  background: rgba(0, 255, 0, 0.1);
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 255, 0, 0.2);
}

.download-btn:active {
  transform: translateY(0);
  box-shadow: 0 3px 10px rgba(0, 255, 0, 0.3);
}

.btn-icon {
  font-size: 1.1rem;
  filter: drop-shadow(0 0 3px rgba(0, 255, 0, 0.5));
}

/* Responsive Design */

/* Large desktops (1200px+) */
@media (min-width: 1200px) {
  .info-box {
    max-width: 750px;
    min-width: 600px;
    min-height: 240px;
    padding: 30px 40px;
  }
  
  .info-box h3 {
    font-size: 1.6rem;
    margin-bottom: 30px;
  }
  
  .info-content {
    gap: 20px;
  }
  
  .info-label {
    font-size: 1.1rem;
  }
  
  .info-value {
    font-size: 1.2rem;
  }
  
  .download-buttons {
    gap: 20px;
    margin-top: 25px;
  }
  
  .download-btn {
    padding: 12px 20px;
    font-size: 1rem;
    min-width: 160px;
  }
  
  .btn-icon {
    font-size: 1.2rem;
  }
}

/* Large tablets (769px - 1199px) */
@media (max-width: 1199px) and (min-width: 769px) {
  .info-box {
    max-width: 600px;
    min-width: 450px;
    min-height: 200px;
    padding: 22px 30px;
  }
  
  .info-box h3 {
    font-size: 1.4rem;
  }
  
  .info-content {
    gap: 16px;
  }
  
  .info-label {
    font-size: 0.95rem;
  }
  
  .info-value {
    font-size: 1.05rem;
  }
  
  .download-buttons {
    gap: 18px;
    margin-top: 22px;
  }
  
  .download-btn {
    padding: 10px 18px;
    font-size: 0.95rem;
    min-width: 150px;
  }
}

/* Portrait tablets (481px - 768px) */
@media (max-width: 768px) and (min-width: 481px) {
  .info-box {
    max-width: 100%;
    min-width: 350px;
    min-height: 180px;
    padding: 20px 28px;
  }
  
  .info-box h3 {
    font-size: 1.3rem;
    margin-bottom: 20px;
  }
  
  .info-content {
    gap: 14px;
  }
  
  .info-label {
    font-size: 0.9rem;
  }
  
  .info-value {
    font-size: 1rem;
    margin-left: 10px;
  }
  
  .download-buttons {
    gap: 15px;
    margin-top: 18px;
  }
  
  .download-btn {
    padding: 9px 15px;
    font-size: 0.85rem;
    min-width: 130px;
  }
}

/* Mobile devices (up to 480px) */
@media (max-width: 480px) {
  .info-box {
    min-width: 300px;
    max-width: 100%;
    min-height: 240px;
    padding: 18px 22px;
  }
  
  .info-box h3 {
    font-size: 1.1rem;
    margin-bottom: 18px;
  }
  
  .info-content {
    gap: 12px;
  }
  
  .info-label {
    font-size: 0.85rem;
  }
  
  .info-value {
    font-size: 0.95rem;
    margin-left: 8px;
  }
  
  .download-buttons {
    gap: 12px;
    margin-top: 15px;
  }
  
  .download-btn {
    padding: 8px 12px;
    font-size: 0.8rem;
    min-width: 120px;
    max-width: 140px;
  }
  
  .btn-icon {
    font-size: 1rem;
  }
}

/* Extra small mobile (up to 360px) */
@media (max-width: 360px) {
  .info-box {
    min-width: 280px;
    min-height: 220px;
    padding: 15px 18px;
  }
  
  .info-box h3 {
    font-size: 1.1rem;
    margin-bottom: 15px;
  }
  
  .info-content {
    gap: 10px;
  }
  
  .info-label {
    font-size: 0.8rem;
  }
  
  .info-value {
    font-size: 0.9rem;
  }
  
  .download-buttons {
    gap: 8px;
    margin-top: 12px;
  }
  
  .download-btn {
    padding: 6px 10px;
    font-size: 0.75rem;
    min-width: 100px;
    letter-spacing: 0.02em;
  }
  
  .btn-icon {
    font-size: 0.9rem;
  }
}

/* Very small screens - Stack vertically if needed */
@media (max-width: 300px) {
  .download-buttons {
    flex-direction: column;
    gap: 10px;
    align-items: center;
  }
  
  .download-btn {
    width: 100%;
    max-width: 200px;
  }
}

/* Animations */
@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

@keyframes scan {
  0% {
    top: 0;
  }
  100% {
    top: 100%;
  }
}

@keyframes blink {
  0%, 50% {
    opacity: 1;
  }
  51%, 100% {
    opacity: 0;
  }
}

/* Custom Scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #000;
}

::-webkit-scrollbar-thumb {
  background: #00ff00;
  border-radius: 4px;
}

::-webkit-scrollbar-thumb:hover {
  background: #00cc00;
}

/* Responsive Design */
@media (max-width: 640px) {
  .hacker-container {
    padding: 0.5rem; /* Reduced padding */
  }
  
  .main-container {
    padding: 1rem; /* Reduced from 1.5rem */
    min-width: 16rem; /* Reduced from 20rem */
  }
  
  .title {
    font-size: 1rem; /* Reduced from 1.25rem */
  }
}

@media (max-width: 768px) {
  .info-title {
    font-size: 1.1rem;
  }
  
  .info-body {
    padding: 20px;
    min-height: 150px;
  }
  
  .info-item p {
    font-size: 1rem;
  }
  
  .info-item strong {
    min-width: 70px;
    font-size: 0.9rem;
  }
  
  .info-box {
    max-width: 100%;
    min-width: 300px; /* Reduced for tablets */
    min-height: 160px;
    padding: 20px 25px;
  }
  
  /* Keep vertical layout */
  .info-content {
    flex-direction: column; /* Force vertical */
    gap: 12px;
  }
  
  .info-box h3 {
    font-size: 1.2rem;
    margin-bottom: 15px;
  }
  
  .info-label {
    font-size: 0.85rem;
  }
  
  .info-value {
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .info-box {
    min-width: 250px; /* Further reduced for small phones */
    min-height: 140px;
    padding: 15px 20px;
  }
  
  .info-box h3 {
    font-size: 1.1rem;
    margin-bottom: 12px;
  }
  
  /* Keep vertical layout */
  .info-content {
    flex-direction: column; /* Force vertical */
    gap: 10px;
  }
  
  .info-label {
    font-size: 0.8rem;
  }
  
  .info-value {
    font-size: 0.85rem;
  }
}

/* Extra wide screens */
@media (min-width: 1200px) {
  .info-box {
    max-width: 750px; /* Even wider on large screens */
    min-height: 240px;
    padding: 30px 40px;
  }
  
  /* Keep vertical layout */
  .info-content {
    flex-direction: column; /* Force vertical */
    gap: 18px;
  }
}

/* Hacker Theme Overrides - Change all #00ff00 colors to #32556e */
.info-card.hacker-theme .corner-top-left,
.info-card.hacker-theme .corner-top-right,
.info-card.hacker-theme .corner-bottom-left,
.info-card.hacker-theme .corner-bottom-right {
  border-color: #32556e;
}

.info-card.hacker-theme .main-container {
  border-color: #32556e;
  box-shadow: 0 0 20px rgba(50, 85, 110, 0.2);
}

.info-card.hacker-theme .glow-border-outer {
  border-color: #32556e;
}

.info-card.hacker-theme .glow-border-inner {
  border-color: rgba(50, 85, 110, 0.3);
}

.info-card.hacker-theme .scanning-line {
  background: linear-gradient(90deg, transparent, #32556e, transparent);
}

.info-card.hacker-theme .title {
  color: #32556e;
  text-shadow: 0 0 10px #32556e, 0 0 20px #32556e, 0 0 30px #32556e;
}

.info-card.hacker-theme .info-title {
  color: #32556e;
  text-shadow: 0 0 10px rgba(50, 85, 110, 0.5);
}

.info-card.hacker-theme .info-title::after {
  background: #32556e;
  box-shadow: 0 0 10px rgba(50, 85, 110, 0.5);
}

.info-card.hacker-theme .info-item strong,
.info-card.hacker-theme .info-label {
  color: #32556e;
  text-shadow: 0 0 5px rgba(50, 85, 110, 0.3);
}

.info-card.hacker-theme .side-indicator {
  background: #32556e;
}

.info-card.hacker-theme .download-btn {
  color: #32556e;
  border: 2px solid rgba(50, 85, 110, 0.3);
  text-shadow: 0 0 5px rgba(50, 85, 110, 0.3);
}

.info-card.hacker-theme .download-btn:hover {
  border-color: #32556e;
  background: rgba(50, 85, 110, 0.1);
  box-shadow: 0 6px 20px rgba(50, 85, 110, 0.2);
}

.info-card.hacker-theme .download-btn:active {
  box-shadow: 0 3px 10px rgba(50, 85, 110, 0.3);
}

.info-card.hacker-theme .btn-icon {
  filter: drop-shadow(0 0 3px rgba(50, 85, 110, 0.5));
}

/* Custom Scrollbar for hacker theme */
.info-card.hacker-theme::-webkit-scrollbar-thumb {
  background: #32556e;
}

.info-card.hacker-theme::-webkit-scrollbar-thumb:hover {
  background: #4a6b8a;
}