/* src/CSS/App.css */
body {
  margin: 0;
  background-color: #000;
  font-family: 'Courier New', monospace;
}

.topNav {
  background-color: #111;
  padding: 15px;
  display: flex;
  justify-content: center;
  gap: 40px;
  box-shadow: 0 2px 8px #00ff00;
}

.topNav a {
  color: #00ff00;
  text-decoration: none;
  font-weight: bold;
  font-size: 1rem;
}

.topNav a:hover {
  text-decoration: underline;
}

.appContainer {
  text-align: center;
}
